<script>
    import { Accordion, AccordionItem, Alert, Label, Row, Col, Badge, ButtonGroup, Input } from "sveltestrap";
    import items from "./data/items.json";
    import enchantments from "./data/enchantments.json";

    let itemsAndEnchantments = [];

    let radioGroupValues = [];
    let radioGroupIncompatibles = [];

    prepareItemsAndEnchantments();

    function prepareItemsAndEnchantments()
    {
        Object.entries(items).forEach(itemPair => {
            const [itemKey, item] = itemPair;

            itemsAndEnchantments[itemKey] = [];
            radioGroupValues[itemKey] = [];
            radioGroupIncompatibles[itemKey] = [];

            Object.entries(enchantments).forEach(enchantmentPair => {
                const [enchantmentKey, enchantment] = enchantmentPair;

                if (enchantment.items.includes(itemKey)) {

                    itemsAndEnchantments[itemKey].push(enchantmentKey);
                    radioGroupValues[itemKey][enchantmentKey] = 0;
                    radioGroupIncompatibles[itemKey][enchantmentKey] = [];
                }
            });
        });
    }

    function chooseEnchantmentLevel(chosenItemKey, chosenEnchantmentKey)
    {
        resetIncompatibleEnchantments(chosenItemKey, chosenEnchantmentKey);

        function resetIncompatibleEnchantments(itemKey, enchantmentKey)
        {            
            radioGroupIncompatibles[itemKey][enchantmentKey].forEach(tmpIncompatible => {

                // remove incompatibilities regarding the pressed enchantment
                removeIncompatibilities(enchantments[enchantmentKey].incompatibles, tmpIncompatible);

                // remove incompabilities that were on the pressed enchantment
                removeIncompatibilities(itemsAndEnchantments[itemKey], tmpIncompatible);
            });

            // remove incompatibilitis of pressed enchantment
            radioGroupIncompatibles[itemKey][enchantmentKey] = [];

            // set new incompatibilities
            enchantments[enchantmentKey].incompatibles.forEach(incompatibleEnchantmentKey => {
                radioGroupValues[itemKey][incompatibleEnchantmentKey] = 0;
                if (radioGroupIncompatibles[itemKey][incompatibleEnchantmentKey] != undefined && !radioGroupIncompatibles[itemKey][incompatibleEnchantmentKey].includes(enchantmentKey))
                {
                    radioGroupIncompatibles[itemKey][incompatibleEnchantmentKey].push(enchantmentKey);
                }
            });

            // trigger update, since push does not trigger it
            radioGroupIncompatibles[itemKey][incompatibleEnchantmentKey] = radioGroupIncompatibles[itemKey][incompatibleEnchantmentKey];

            function removeIncompatibilities(myIncompatibilities, tmpIncompatible)
            {
                myIncompatibilities.forEach(incompatibleEnchantmentKey => {
                    if (radioGroupIncompatibles[itemKey][incompatibleEnchantmentKey].includes(tmpIncompatible))
                    {
                        radioGroupIncompatibles[itemKey][incompatibleEnchantmentKey].splice(radioGroupIncompatibles[itemKey][incompatibleEnchantmentKey].indexOf(tmpIncompatible), 1);
                    }
                });
            }
        }
    }
</script>

<h3>Level Calculator</h3>

<Alert color="warning">
    <b>Work in progress!</b><br />
    This section is currently not working.
</Alert>

<Accordion>
    {#each Object.entries(itemsAndEnchantments) as [itemKey, enchantmentKeys]}
        <AccordionItem header="{items[itemKey]}" class="item-image item-image-{itemKey}">
            {#each enchantmentKeys as enchantmentKey}
                <Row class="{radioGroupIncompatibles[itemKey][enchantmentKey].length > 0 ? 'text-decoration-line-through text-secondary' : ''}">
                    <Label for="{itemKey}-{enchantmentKey}-control" sm=12 md=4 lg=3>{enchantmentKey}</Label>
                    <Col sm=12 md=4 lg=3>
                        <ButtonGroup>
                            {#each Array(enchantments[enchantmentKey].levelMax) as _, i}
                                <Input type="radio" class="btn-check" bind:group={radioGroupValues[itemKey][enchantmentKey]} id="{itemKey}-{enchantmentKey}-{i + 1}" value="{i + 1}" on:change={() => chooseEnchantmentLevel(itemKey, enchantmentKey)} />    
                                <Label class="btn btn-outline-primary {radioGroupValues[itemKey][enchantmentKey] == i + 1 ? 'active' : ''} " for="{itemKey}-{enchantmentKey}-{i + 1}">{i + 1}</Label>
                            {/each}
                        </ButtonGroup>
                    </Col>
                    <Col sm=12 md=4 lg=6>
                        {#if radioGroupIncompatibles[itemKey][enchantmentKey].length > 0}
                            <Badge color=danger>Incompatible with: {radioGroupIncompatibles[itemKey][enchantmentKey].join(", ")}</Badge>
                        {/if}
                    </Col>
                </Row>
            {/each}
        </AccordionItem>
    {/each}
</Accordion>

